.game-board{
    /* height: 40; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.board{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.row{
    display: flex;
    flex-direction: row;
}

.letter{
    border: 1px solid rgb(255, 255, 255);
    width: 50px;
    height: 50px;
    margin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    color: white;
}

#correct{
    background-color: green;
}

#almost{
    background-color: orange;
}

#error{
    background-color: grey;
}

#disabled {
    background-color: #3a393c;
  }

.keyboard{
    margin-top: 50px;
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: center;
    max-width: 100vw;
}

.line1, .line2, .line3{

    display: flex;
    flex-direction: row;
    max-width: 100vw;
}

.key{
    background-color: grey;
    width: 40px;
    height: 50px;
    margin: 5px;
    border-radius: 8px;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

#big{
    width:80px
}

.game-over{
    color: white;
}

.game-over h2{
    font-size: 35px;
}


@media (max-width: 600px) {
    .letter{
        border: 1px solid rgb(255, 255, 255);
        width: 30px;
        height: 30px;
        margin: 5px;
        font-size: 20px;
    }
  
    .keyboard{
        margin-top: 30px;
        max-width: 100vw;
    }

    .key{
        background-color: grey;
        width: 25px;
        height: 35px;
        margin: 5px;
        border-radius: 4px;
        font-size: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }
    
    #big{
        width:50px
    }
  }
  